body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#nav {
  display: inline-block;
}

#nav.ant-menu-horizontal>.ant-menu-item-selected, #nav.ant-menu-horizontal>.ant-menu-item-active{
  border-top: 2px solid #1890ff;
  border-bottom: 2px solid transparent;
}

.header-logo {
  height: 50px;
  line-height: 50px;
}

.menu {
  display: flex;
  flex-direction: row;
}

.menu-sider{
  top: 6px;
  right: 0px;
}

.grid {
  display: flex;
  flex-direction: row;
  margin: 20px auto;
  width: 100%;
}

.grid-item {
  flex-direction: column;
  width: 300px;
  margin-bottom: 5px;
}

.grid-item img {
  display: block;
  max-width: 100%;
}

.ant-layout-content {
  background: white;
}

.ant-layout-sider-zero-width-trigger {
  z-index: 10;
}
